import { mapActions } from 'vuex';

export default {
  name: 'Questionare',
  props: ['action'],
  data() {
    return {};
  },
  components: {},
  directives: {},
  computed: {},
  methods: {
    async sendQuestionare() {
      let res = await this.confirm(this.$i18n.t('Send Questionare?'));
      if (res) {
        this.updateRoomBot({
          roomId: this.$store.state.roomActive._id,
          roomInfo: {
            botState: this.action.extra.botState
          }
        });
      }
    },
    ...mapActions(['confirm', 'updateRoomBot'])
  }
};



