import { mapActions } from 'vuex';
import VueTagsInput from '@johmun/vue-tags-input';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'SetTags',
  props: ['defaultSetTags'],
  data() {
    return {
      tag: '',
      tags: []
    };
  },
  components: {
    VueTagsInput,
    Modal
  },
  computed: {
    autocompleteItems() {
      let items = [];
      this.$store.state.roomList.forEach(room => {
        if (room.pref && room.pref.tags) {
          items = items.concat(
            room.pref.tags.map(tag => {
              return { text: tag };
            })
          );
        }
      });

      return items;
    },
    filteredItems() {
      return this.autocompleteItems.filter(i => {
        return i.text.toLowerCase().indexOf(this.tag.toLowerCase()) !== -1;
      });
    }
  },
  mounted() {
    if (this.defaultSetTags) {
      this.tags = this.defaultSetTags.map(tag => {
        return {
          text: tag,
          tiClasses: ['ti-valid']
        };
      });
    }
  },
  methods: {
    async save() {
      let tags = this.tags.map(tag => {
        return tag.text;
      });
      if (this.tag) {
        if (!~tags.indexOf(this.tag)) {
          tags.push(this.tag);
        }
      }
      this.$emit('save', tags);
      this.cancel();
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions([])
  }
};



