import ClickOutside from 'vue-click-outside';
import { mapActions } from 'vuex';

export default {
  name: 'RoomFolder',
  props: ['folder', 'isShowUnread', 'isShowTime'],
  data() {
    return {
      isShowActionsList: false,
      isShowAction: true,
      isShowDelete: true,
      isShowRename: true,
      isClick: false,
      touchstartX: 0,
      touchstartY: 0
    };
  },
  components: {},
  directives: {
    ClickOutside
  },
  computed: {},
  methods: {
    roomClick() {
      this.folder.open = !this.folder.open;
    },
    hideActionsList() {
      this.isShowActionsList = false;
    },
    onTouchstart(e) {
      this.isClick = true;
      this.touchstartX = e.touches ? e.touches[0].clientX : e.clientX;
      this.touchstartY = e.touches ? e.touches[0].clientY : e.clientY;
    },
    onTouchmove(e) {
      if (this.isClick) {
        let clientX = e.touches ? e.touches[0].clientX : e.clientX;
        let clientY = e.touches ? e.touches[0].clientY : e.clientY;
        if (
          Math.abs(clientX - this.touchstartX) > 10 ||
          Math.abs(clientY - this.touchstartY) > 10
        ) {
          this.isClick = false;
          this.$emit('contextmenu');
        }
      }
    },
    onTouchend() {
      if (this.isClick) {
        this.isClick = false;
        this.roomClick();
      }
    },
    ...mapActions(['deleteRoomFolderList'])
  }
};



