import { mapActions } from 'vuex';
import axios from 'axios';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomChatOutputMessage from '@/components/RoomChat/RoomChatOutput/RoomChatOutputMessage/index.vue';
import RoomChatOutputSilder from '@/components/RoomChat/RoomChatOutput/RoomChatOutputSilder/index.vue';

export default {
  name: 'RoomChatOutput',
  data() {
    return {
      unsubscribe: null,
      slider: {
        show: false,
        default: ''
      }
    };
  },
  components: {
    Scrollbar,
    RoomChatOutputMessage,
    RoomChatOutputSilder
  },
  computed: {
    searchData() {
      return this.$store.state.roomChatSearch;
    },
    messages() {
      return this.$store.state.roomChatSearch.messages;
    },
    isMobile() {
      return this.$store.state.isMobile;
    }
  },
  mounted() {
    this.unsubscribe = this.$store.subscribe((mutation, state) => {
      switch (mutation.type) {
        case 'gotoMessage':
          this.$nextTick(() => {
            this.gotoMessage(
              state.roomChatSearch.result[state.roomChatSearch.indexOfResult]
            );
          });
          break;
      }
    });
  },
  beforeDestroy() {
    this.unsubscribe();
  },
  methods: {
    resize() {},
    showSilder(message) {
      this.slider.default = message.id;
      this.slider.show = true;
    },
    closeSilder() {
      this.slider.show = false;
    },
    setReply(message) {
      this.$emit('setReply', message);
    },
    gotoMessage(messageId) {
      let messageElement = document.getElementById(`message-${messageId}`);
      let contentBox = document.querySelector(
        `#message-${messageId} .contentBox`
      );
      if (messageElement) {
        this.$refs.list.scrollElement(messageElement);
        if (contentBox.classList) {
          contentBox.classList.add('shake');
          setTimeout(() => {
            contentBox.classList.remove('shake');
          }, 2000);
        }
      }
    },
    async loadMoreBefore() {
      if (this.roomChatSearch.loadBefore) {
        return;
      }
      let messagesCancelSource = axios.CancelToken.source();
      this.setRoomChatSearch({
        messagesCancelSource: messagesCancelSource,
        loadBefore: true
      });
      let resp = await this.roomChatSearch({
        roomId: this.$store.state.roomActive._id,
        messageId: this.messages[0]._id,
        cancelToken: messagesCancelSource.token,
        before: 5,
        after: 0
      });
      resp.data.pop();
      let messages = JSON.parse(JSON.stringify(this.messages));
      messages.unshift(...resp.data);
      this.setRoomChatSearch({
        messagesCancelSource: null,
        messages: messages,
        hasBefore: !(resp.data.length < 5),
        loadBefore: false
      });
    },
    async loadMoreAfter() {
      if (this.roomChatSearch.loadAfter) {
        return;
      }
      let messagesCancelSource = axios.CancelToken.source();
      this.setRoomChatSearch({
        messagesCancelSource: messagesCancelSource,
        loadAfter: true
      });
      let resp = await this.roomChatSearch({
        roomId: this.$store.state.roomActive._id,
        messageId: this.messages[this.messages.length - 1]._id,
        cancelToken: messagesCancelSource.token,
        before: 0,
        after: 5
      });
      resp.data.shift();
      let messages = JSON.parse(JSON.stringify(this.messages));
      messages.push(...resp.data);
      this.setRoomChatSearch({
        messagesCancelSource: null,
        messages: messages,
        hasAfter: !(resp.data.length < 5),
        loadAfter: false
      });
    },
    ...mapActions(['setRoomChatSearch', 'roomChatSearch'])
  }
};



