export default {
  vue: null,
  config: {
    debug: true,
    domain: '',
    authBase: '',
    authClientId: '',
    urlPreviewApi: 'https://chatkit.co/url',
    clientKey: '',
    token: '',
    privateKey: '',
    bucketName: '',
    googleApiKey: '',
    appLogo: 'https://i.imgur.com/gchEcBi.png',
    appName: 'IMKit Demo',
    lang: 'en',
    avatarHeaders: [],
    downloadFileHeaders: [],
    layout: {
      list: true,
      info: true
    },
    chat: {
      modeTyping: 0,
      showHeader: true,
      dragdropUpload: {
        enable: true,
        extra: {
          accept: [
            'application/pdf',
            'audio/mp3',
            'image/png',
            'image/jpeg',
            'video/mp4',
            'video/quicktime'
          ],
          image: {
            limitSize: 10,
            thumbnailSize: 1500
          },
          video: {
            limitSize: 100,
            thumbnailSize: 1500
          },
          other: {
            limitSize: 10
          }
        }
      },
      actions: [
        {
          type: 'file',
          pcEnable: true,
          padEnable: true,
          mobileEnable: true,
          text: 'Send File',
          extra: {
            accept: [
              'application/pdf',
              'audio/mp3',
              'image/png',
              'image/jpeg',
              'video/mp4',
              'video/quicktime'
            ],
            image: {
              limitSize: 10,
              thumbnailSize: 1500
            },
            video: {
              limitSize: 100,
              thumbnailSize: 1500
            },
            other: {
              limitSize: 10
            }
          }
        },
        {
          type: 'group',
          enable: true,
          mobileEnable: true,
          text: '',
          actions: [
            {
              type: 'emoji',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Emoji'
            },
            {
              type: 'sticker',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Sticker'
            },
            {
              type: 'image',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Image',
              extra: {
                accept: ['image/png', 'image/jpeg'],
                image: {
                  limitSize: 10,
                  thumbnailSize: 1500
                }
              }
            },
            {
              type: 'video',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Video',
              extra: {
                accept: ['video/mp4', 'video/quicktime'],
                video: {
                  limitSize: 100,
                  thumbnailSize: 1500
                }
              }
            },
            {
              type: 'recorder',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Recorder',
              extra: {
                limitSeconds: 60
              }
            },
            {
              type: 'location',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Location'
            },
            {
              type: 'paymentRequest',
              pcEnable: false,
              padEnable: false,
              mobileEnable: false,
              text: 'Send Payment Request',
              extra: {
                currencies: ['TWD', 'USD'],
                requestApi: 'https://pinchat.cc/api/request_payment',
                paymentBy: [
                  {
                    key: 'paypal',
                    text: 'PayPal'
                  },
                  {
                    key: 'tappay',
                    text: 'TapPay'
                  },
                  {
                    currencies: ['TWD'],
                    key: 'newebpay',
                    text: '藍新'
                  }
                ]
              }
            },
            {
              type: 'questionare',
              pcEnable: true,
              padEnable: true,
              mobileEnable: true,
              text: 'Send Questionare',
              extra: {
                botState: 'LINGTELLI::'
              }
            }
          ]
        }
      ],
      actionsPosition: {
        pc: 'bottom',
        pad: 'right',
        mobile: 'right'
      },
      limitTextLength: 100,
      sliderReturnMode: 'close', // back or close
      asyncUrlPreview: true,
      colors: {
        header: {
          background: null,
          color: null
        },
        self: {
          background: null,
          color: null,
          borderColor: null
        },
        other: {
          background: null,
          color: null,
          borderColor: null
        },
        system: {
          background: null,
          color: null,
          borderColor: null
        }
      },
      thumbnailSize: 500,
      readReceipt: true,
      reply: true,
      forward: true,
      recall: true,
      audioDownload: true,
      sticker: [
        {
          folder: 'funfunfamily',
          icon: 'FunFunFamily-1.png',
          prefix: 'FunFunFamily-',
          suffix: '.png',
          count: 40
        }
      ]
    },
    list: {
      sort: null,
      maxCount: null,
      lockText: '',
      lockOnClick: function() {},
      loadAllRoom: true,
      colors: {
        header: {
          background: null,
          color: null
        }
      },
      memberCount: true,
      logout: {
        enable: false,
        event: null
      },
      sticky: true,
      hidden: true,
      folder: true,
      tags: true,
      createRoom: false
    },
    info: {
      bot: {
        enable: false
      },
      colors: {
        header: {
          background: null,
          color: null
        }
      },
      edit: {
        room: {
          name: true,
          description: true,
          cover: true
        },
        leave: true,
        remove: true,
        createRoom: true,
        invite: true
      },
      openMembersList: false,
      iframes: []
    },
    events: {
      onSetRoomActiveDefault: true,
      onSetRoomActive: null,
      onAjaxError: () => {}
    },
    autoEnterRoom: true,
    autoEnterRoomMobile: false,
    defaultRoom: '',
    createRoom: {
      id: true,
      name: true,
      description: true,
      cover: true
    },
    alwaysMute: false,
    changeTitle: true,
    i18n: {
      en: {},
      'zh-tw': {}
    },
    special: {
      renderRoomDescription: null
    },
    notificationIcon: '',
    FCMConfig: null
  },
  // window 祖先，拉最上層改 title 及判斷分頁是否檢視中用
  pageAncestor: {
    el: null, // 祖先元件
    title: 'IMLit Demo' // 預設的 title
  },
  pageLoading: true, // 打開頁面時顯示載入中，直到列表載入完成
  api: {},
  socket: {},
  connect: true, // 是否與 socket 連接，判斷斷線用，初始設 true 避免頁面一載入就顯示斷線訊息
  i18n: {},
  alert: null,
  confirm: null,
  roomList: [],
  roomFolderList: [], // 房間資料夾
  roomListLoader: true, // 是否顯示圈圈
  roomListSearchResult: null, // 列表搜尋結果
  roomListSearchMessages: null, // 列表搜尋結果(訊息)
  roomChatList: [],
  roomChatRight: '', // search 之類要顯示在聊天室右邊的東西
  roomChatSearch: {
    showSearchBar: false,
    showInput: false,
    keyword: '',
    resultCancelSource: null,
    result: [],
    indexOfResult: 0,
    messagesCancelSource: null,
    messages: [],
    showMessages: false,
    hasBefore: false,
    hasAfter: false,
    loadBefore: false,
    loadAfter: false
  },
  roomActive: {},
  sendingList: [],
  client: {},
  clientPref: [],
  forward: {
    messages: [],
    rooms: [],
    selectMessages: false,
    selectRooms: false,
    sending: false
  },
  typingList: [],
  supportNotification: false, // 是否支援推播
  enableNotification: false, // 是否開啟推播
  isMobile: false, // 是否有 touch 事件
  widthPad: 1024, // Pad 版對大寬度
  widthPhone: 640 // Phone 版對大寬度
};



