import Avatar from '@/components/Components/Avatar/index.vue';
import Checkbox from '@/components/Components/Checkbox/index.vue';
import ClickOutside from 'vue-click-outside';
import { mapActions } from 'vuex';

export default {
  name: 'RoomListItem',
  props: [
    'room',
    'active',
    'isShowTime',
    'isShowUnread',
    'isShowCheckbox',
    'isShowThumbtack',
    'isShowDrag',
    'checked',
    'lock'
  ],
  data() {
    return {
      isShowActionsList: false,
      isClick: false,
      touchstartX: 0,
      touchstartY: 0
    };
  },
  components: {
    Avatar,
    Checkbox
  },
  directives: {
    ClickOutside
  },
  computed: {
    isShowAction() {
      return (
        this.isShowSticky ||
        this.isShowHide ||
        this.isShowFolder ||
        this.isShowTags
      );
    },
    isShowSticky() {
      return this.$store.state.config.list.sticky;
    },
    isShowHide() {
      return this.$store.state.config.list.hidden;
    },
    isShowFolder() {
      return this.$store.state.config.list.folder;
    },
    isShowTags() {
      return this.$store.state.config.list.tags;
    }
  },
  methods: {
    roomClick() {
      if (this.lock) {
        this.$store.state.config.list.lockOnClick();
      } else {
        this.$emit('roomClick');
      }
    },
    hideActionsList() {
      this.isShowActionsList = false;
    },

    onTouchstart(e) {
      this.isClick = true;
      this.touchstartX = e.touches ? e.touches[0].clientX : e.clientX;
      this.touchstartY = e.touches ? e.touches[0].clientY : e.clientY;
    },
    onTouchmove(e) {
      if (this.isClick) {
        let clientX = e.touches ? e.touches[0].clientX : e.clientX;
        let clientY = e.touches ? e.touches[0].clientY : e.clientY;
        if (
          Math.abs(clientX - this.touchstartX) > 10 ||
          Math.abs(clientY - this.touchstartY) > 10
        ) {
          this.isClick = false;
          this.$emit('contextmenu');
        }
      }
    },
    onTouchend() {
      if (this.isClick) {
        this.isClick = false;
        this.roomClick();
      }
    },
    ...mapActions(['setRoomSticky', 'setRoomHidden', 'setRoomFolder'])
  }
};



