import ClickOutside from 'vue-click-outside';
import Swiper from 'swiper/dist/js/swiper.js';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';

export default {
  name: 'Sticker',
  props: ['action'],
  data() {
    return {
      isShowBox: false,
      folder: '',
      sliderSticker: null
    };
  },
  components: {
    Scrollbar
  },
  directives: {
    ClickOutside
  },
  computed: {
    config() {
      return this.$store.state.config.chat.sticker;
    },
    sticker() {
      if (!this.folder) {
        return {};
      }
      return this.config.find(s => s.folder === this.folder);
    }
  },
  mounted() {
    this.folder = this.config[0].folder;
  },
  methods: {
    swiperInit() {
      if (!this.sliderSticker) {
        this.sliderSticker = new Swiper(this.$refs.sliderItem, {
          spaceBetween: 0,
          slidesPerView: 'auto',
          touchRatio: 0.2,
          freeMode: true,
          mousewheel: true
        });
      }
    },
    selectFolder(folder) {
      this.folder = folder;
      this.$nextTick(() => {
        this.$refs.list.scrollTop(0);
      });
    },
    open() {
      this.isShowBox = !this.isShowBox;
      this.$nextTick(() => {
        this.swiperInit();
      });
    },
    close() {
      if (!this.isShowBox) {
        return;
      }
      this.isShowBox = false;
      this.$emit('closeActionsGroup');
    },
    send(idx) {
      let msg = {
        messageType: 'sticker',
        sticker: `${
          document.getElementsByTagName('base')[0].href
        }static/sticker/${this.sticker.folder}/${this.sticker.prefix}${idx}${
          this.sticker.suffix
        }`
      };
      this.$emit('outputScrollToBottom');
      this.$emit('sendSticker', msg);
      this.close();
    }
  }
};



