import { mapActions } from 'vuex';
import axios from 'axios';
import lib from '@/lib';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomListItem from '@/components/RoomList/RoomListItem/index.vue';
import AddFolder from './AddFolder/index.vue';
import SelectFolder from './SelectFolder/index.vue';
import SetTags from './SetTags/index.vue';
import RoomFolder from './RoomFolder/index.vue';
import Avatar from '@/components/Components/Avatar/index.vue';

export default {
  name: 'RoomList',
  data() {
    return {
      searchText: '',
      searchTextTimer: null,
      searching: false,
      searchCancelSource: null,
      list: null,
      isShowAddFolder: false,
      isShowSelectFolder: false,
      defaultSelectFolder: null,
      isHideActions: true,
      hideActionsTimer: null,
      isDrag: false,
      dragoverFolder: null,
      dragRoom: null,
      editFolder: null,
      isShowSetTags: false,
      setTagsRoom: null,
      defaultSetTags: [],
      drag: {
        type: null,
        item: null
      },
      dragTimer: null,
      dragMoveTime: 0,
      insertFolder: null,
      insertFolderNull: false,
      moveToFolder: null,
      moveToFolderNull: false
    };
  },
  watch: {
    searchText(val) {
      if (this.searchTextTimer) {
        clearTimeout(this.searchTextTimer);
      }
      this.searchTextTimer = setTimeout(() => {
        this.search();
      }, 300);
    }
  },
  components: {
    Avatar,
    AddFolder,
    SelectFolder,
    SetTags,
    RoomFolder,
    RoomListItem,
    Scrollbar
  },
  computed: {
    roomListSearchResult() {
      return this.$store.state.roomListSearchResult;
    },
    roomListSearchMessages() {
      return this.$store.state.roomListSearchMessages;
    },
    listScroll() {
      return this.$refs.list;
    },
    roomFolderList() {
      return this.$store.state.roomFolderList.map(folder => {
        folder.unread = this.$store.state.roomList.reduce((prev, room) => {
          return prev + (room.folder === folder.key ? room.unread : 0);
        }, 0);
        return folder;
      });
    }
  },
  mounted() {
    this.hideActions();
    document.body.addEventListener('mousemove', this.onMousemove, {
      passive: false
    });
    document.body.addEventListener('touchmove', this.onMousemove, {
      passive: false
    });
    document.body.addEventListener('mouseup', this.onMouseup);
    document.body.addEventListener('touchend', this.onMouseup);
    this.dragTimer = setInterval(() => {
      if (!this.drag.item) {
        return;
      }

      let listClientHeight = this.$refs.list.clientHeight();
      let listScrollTop = this.$refs.list.scrollTop();
      let listScrollBottom = this.$refs.list.scrollBottom();
      let y = this.drag.item.dragTop - listScrollTop;
      if (y < 50) {
        let diff = Math.max(listScrollTop - 10, 0) - listScrollTop;
        this.$refs.list.scrollTop(listScrollTop + diff);
        this.drag.item.dragTop += diff;
      } else if (y > listClientHeight - 50) {
        let diff = Math.max(listScrollBottom - 10, 0) - listScrollBottom;
        this.$refs.list.scrollBottom(listScrollBottom + diff);
        this.drag.item.dragTop -= diff;
      }
    }, 10);
  },
  beforeDestroy() {
    document.body.removeEventListener('mousemove', this.onMousemove, {
      passive: false
    });
    document.body.removeEventListener('touchmove', this.onMousemove, {
      passive: false
    });
    document.body.removeEventListener('mouseup', this.onMouseup);
    document.body.removeEventListener('touchend', this.onMouseup);
    clearInterval(this.dragTimer);
  },
  methods: {
    async search() {
      this.setRoomListSearchResult(null);
      this.setRoomListSearchMessages(null);
      if (this.searchCancelSource) {
        this.searchCancelSource.cancel('Search canceled by the user.');
        this.searchCancelSource = null;
      }
      if (this.searchText === '') {
        return;
      }
      this.searchCancelSource = axios.CancelToken.source();
      let resp = await this.roomListSearch({
        type: ['messages', 'roomName', 'roomMember'],
        keyword: this.searchText,
        room: null,
        cancelToken: this.searchCancelSource.token
      });
      this.searchCancelSource = null;
      this.setRoomListSearchResult(resp);
    },
    onSearchRoomClick(room) {
      this.setRoomChatSearch({
        showSearchBar: false,
        showInput: false,
        keyword: '',
        resultCancelSource: null,
        result: [],
        indexOfResult: 0,
        messagesCancelSource: null,
        messages: [],
        showMessages: false,
        hasBefore: false,
        hasAfter: false,
        loadBefore: false,
        loadAfter: false
      });
      this.roomClick(room);
    },
    async onSearchMessagesClick(room) {
      if (this.roomListSearchResult) {
        this.setRoomListSearchMessages(this.roomListSearchResult.messages);
      }
      this.setRoomListSearchResult(null);
      this.roomClick(room.room);
      let messagesCancelSource = axios.CancelToken.source();
      this.setRoomChatSearch({
        showSearchBar: true,
        showInput: false,
        keyword: this.searchText,
        resultCancelSource: null,
        result: room.messages,
        indexOfResult: 0,
        messagesCancelSource: messagesCancelSource,
        messages: [],
        showMessages: true,
        hasBefore: false,
        hasAfter: false,
        loadBefore: false,
        loadAfter: false
      });
      let resp = await this.roomChatSearch({
        roomId: room.room._id,
        messageId: room.messages[0],
        cancelToken: messagesCancelSource.token
      });
      this.setRoomChatSearch({
        messagesCancelSource: null,
        messages: resp.data,
        hasBefore: true,
        hasAfter: true
      });
      this.$nextTick(() => {
        this.$store.commit('gotoMessage');
      });
    },
    onListScroll() {
      if (
        this.$store.state.roomList &&
        !this.$store.state.roomListLoading && // 正在讀取中
        this.$store.state.roomListLoader && // 有更多
        this.listScroll.scrollBottom() < 10
      ) {
        this.getRoomList();
      }
      // 隱藏 add 按鈕
      // this.hideActions();
    },
    hideActions() {
      if (this.hideActionsTimer) {
        clearTimeout(this.hideActionsTimer);
        this.hideActionsTimer = null;
      }
      this.isHideActions = true;
      this.hideActionsTimer = setTimeout(() => {
        this.isHideActions = false;
      }, 2000);
    },
    notificationsOnClick() {
      this.$fcm
        .requestPermission()
        .then(() => {
          this.$fcm.getToken().then(refreshedToken => {
            if (this.$store.state.enableNotification) {
              // 關閉
              this.unsubscribe(refreshedToken);
            } else {
              // 開啟
              this.subscribe(refreshedToken);
            }
          });
        })
        .catch(error => {
          this.$log.error('[FCM requestPermission]', error);
        });
    },
    logout() {
      let logoutEvent = this.$store.state.config.list.logout.event;
      if (this.$store.state.enableNotification) {
        this.$fcm.getToken().then(refreshedToken => {
          this.unsubscribe(refreshedToken);
          if (logoutEvent) {
            logoutEvent();
          }
        });
      } else {
        if (logoutEvent) {
          logoutEvent();
        }
      }
    },
    roomClick(room) {
      if (room.id !== this.$store.state.roomActive.id) {
        this.setRoomActive(room.id);
      }
    },
    dragstart(room) {
      this.dragRoom = room;
    },
    dragend() {
      this.dragRoom = null;
    },
    dragover(e, folderId) {
      e.preventDefault();
      this.dragoverFolder = folderId;
    },
    dragleave(e) {
      e.preventDefault();
      this.dragoverFolder = null;
    },
    dragdrop(e, folderKey) {
      e.preventDefault();
      this.saveSelectFolder(folderKey);
    },
    hideSelectFolder() {
      this.isShowSelectFolder = false;
      this.dragRoom = null;
    },
    showAddFolder(folder = null) {
      this.editFolder = folder;
      this.isShowAddFolder = true;
    },
    showSelectFolder(room) {
      this.isShowSelectFolder = true;
      this.dragRoom = room;
      this.defaultSelectFolder = room.folder;
    },
    saveSelectFolder(folderKey) {
      this.dragRoom.folder = folderKey;
      this.setRoomFolder({
        room: this.dragRoom,
        folderKey: folderKey
      });
      this.dragRoom = null;
    },
    showSetTags(room) {
      this.isShowSetTags = true;
      this.setTagsRoom = room;
      this.defaultSetTags = room.pref.tags;
    },
    hideSetTags() {
      this.isShowSetTags = false;
      this.setTagsRoom = null;
    },
    saveSetTags(tags) {
      this.setRoomTags({
        room: this.setTagsRoom,
        tags
      });
      this.hideSetTags();
    },
    onFolderContextmenu(folder) {
      this.drag.type = 'folder';
      this.drag.item = folder;
    },
    onRoomContextmenu(room) {
      this.drag.type = 'room';
      this.drag.item = room;
    },
    onMousemove(e) {
      if (this.drag.type) {
        e.preventDefault();
        e.stopPropagation();
        let time = Date.now();
        if (time - this.dragMoveTime < 50) {
          return;
        }
        this.dragMoveTime = time;

        this.drag.item.drag = true;
        let clientY = e.touches ? e.touches[0].clientY : e.clientY;
        let y =
          clientY -
          document
            .querySelector('.roomList .scrollBar.list')
            .getBoundingClientRect().top;

        this.drag.item.dragTop = y + this.$refs.list.scrollTop();

        if (this.drag.type === 'folder') {
          this.drag.item.open = false;

          // 判定資料夾插入位置
          let lastItem = null;
          for (let i = 0; i < this.roomFolderList.length; i++) {
            if (this.roomFolderList[i].key !== this.drag.item.key) {
              let item = this.$refs[`folder-${this.roomFolderList[i].key}`];
              while (item.length) {
                item = item[0];
              }
              if (item.offsetTop > this.drag.item.dragTop - 35) {
                lastItem = this.roomFolderList[i];
                break;
              }
            }
          }
          this.insertFolderNull = false;
          this.roomFolderList.forEach(f => {
            f.insert = false;
          });
          if (lastItem) {
            lastItem.insert = true;
          } else {
            this.insertFolderNull = true;
          }
          this.insertFolder = lastItem;
        } else if (this.drag.type === 'room') {
          // 判斷房間移動位置
          let folderNull = this.$refs['folder-null'];
          while (folderNull.length) {
            folderNull = folderNull[0];
          }

          if (folderNull.offsetTop < this.drag.item.dragTop) {
            this.moveToFolderNull = true;
            this.moveToFolder = null;
          } else {
            this.moveToFolderNull = false;
            let lastItem = null;
            for (let i = this.roomFolderList.length - 1; i >= 0; i--) {
              let item = this.$refs[`folder-${this.roomFolderList[i].key}`];
              while (item.length) {
                item = item[0];
              }
              if (item.offsetTop < this.drag.item.dragTop) {
                lastItem = this.roomFolderList[i];
                break;
              }
            }

            if (lastItem) {
              this.moveToFolder = lastItem;
            } else {
              this.moveToFolder = null;
            }
          }
          this.roomFolderList.forEach(f => {
            f.moveTo = this.moveToFolder && this.moveToFolder.key === f.key;
          });
        }
      }
    },
    onMouseup(e) {
      if (this.drag.type === 'folder') {
        this.drag.item.drag = false;
        this.drag.item.dragTop = 0;

        this.insertFolderNull = false;
        this.roomFolderList.forEach(f => {
          f.insert = false;
        });

        let dropIdx = this.roomFolderList.findIndex(
          f => f.key === this.drag.item.key
        );

        let dropItem = this.roomFolderList.splice(dropIdx, 1)[0];
        let drawIdx = this.insertFolder
          ? this.roomFolderList.findIndex(f => f.key === this.insertFolder.key)
          : this.roomFolderList.length;
        this.roomFolderList.splice(drawIdx, 0, dropItem);

        lib.room.updateFoldersMeta(this.roomFolderList);
      } else if (this.drag.type === 'room') {
        this.drag.item.drag = false;
        this.drag.item.dragTop = 0;

        this.roomFolderList.forEach(f => {
          f.moveTo = false;
        });

        if (this.moveToFolderNull) {
          this.setRoomFolder({
            room: this.drag.item,
            folderKey: ''
          });
          this.moveToFolderNull = false;
        } else if (this.moveToFolder) {
          this.setRoomFolder({
            room: this.drag.item,
            folderKey: this.moveToFolder.key
          });
        }
      }
      this.drag.type = null;
      this.drag.item = null;
    },
    ...mapActions([
      'roomListSearch',
      'roomChatSearch',
      'setRoomListSearchResult',
      'setRoomListSearchMessages',
      'setRoomChatSearch',
      'hideRoomChatSearch',
      'getRoomList',
      'subscribe',
      'unsubscribe',
      'setRoomActive',
      'setRoomFolder',
      'setRoomTags'
    ])
  }
};



