import { mapActions } from 'vuex';
import StaticMap from 'vue-static-map';

export default {
  name: 'UrlPreview',
  props: ['urlExtra', 'senderType'],
  data() {
    return {
      hidden: false
    };
  },
  components: {
    StaticMap
  },
  computed: {
    isVideo() {
      let ID = '';
      let url = this.urlExtra.link
        .replace(/(>|<)/gi, '')
        .split(/(vi\/|v=|\/v\/|youtu\.be\/|\/embed\/)/);
      if (url[2] !== undefined) {
        ID = url[2].split(/[^0-9a-z_-]/i);
        ID = ID[0];
      } else {
        ID = null;
      }
      return ID;
    }
  },
  mounted() {},
  methods: {
    async hide() {
      let res = await this.confirm({
        text: this.$i18n.t(
          'Are you sure you wish to remove this attachment from the message?'
        ),
        okText: this.$i18n.t('Yes,remove'),
        cancelText: this.$i18n.t('Cancel')
      });
      if (res) {
        this.hidden = true;
      }
    },
    ...mapActions(['confirm'])
  }
};



