import md5 from 'md5';
import store from '@/store';

class Room {
  // 補足缺少的 key，缺少的 key 會導致資料更新時無法同步渲染
  fullKeys(room) {
    let keys = [
      'name',
      'cover',
      'description',
      'lastMessage',
      'isTag',
      'folder',
      'drag',
      'dragTop'
    ];
    for (let key of keys) {
      if (!(key in room)) {
        room[key] = null;
      }
    }
    room.botMode = !!room.botMode;
    room.unread = room.unread || 0;
    if (!room.roomType) {
      if (room.members.length !== 2) {
        room.roomType = 'group';
      } else {
        let hash = md5(
          room.members
            .map(m => {
              return m._id.toLowerCase();
            })
            .sort((a, b) => {
              return a > b ? 1 : -1;
            })
            .join('_')
        );
        if ((hash, hash === room._id)) {
          room.roomType = 'direct';
        } else {
          room.roomType = 'group';
        }
      }
    }

    // 資料夾
    store.state.clientPref
      .find(p => p.key === 'folders-meta')
      .data.forEach(folder => {
        let roomList = store.state.clientPref.find(p => p.key === folder.key);
        if (
          roomList &&
          roomList.data.children.find(
            child => child.id === room._id && child.type === 'room'
          )
        ) {
          room.folder = folder.key;
        }
      });

    if (!room.pref) {
      room.pref = {};
    }
    room.pref = {
      hidden: room.pref.hidden && store.state.config.list.hidden,
      sticky: room.pref.sticky && store.state.config.list.sticky,
      folder: null,
      muted: room.pref.muted || false,
      tags: room.pref.tags || []
    };

    // 客製
    if (store.state.config.special.renderRoomDescription) {
      room.description = store.state.config.special.renderRoomDescription(
        room.description
      );
    }
  }

  resetRoomFolder() {
    store.commit('setClientPref', store.state.clientPref);
    store.state.roomList.forEach(room => {
      room.folder = null;
      store.state.clientPref
        .find(p => p.key === 'folders-meta')
        .data.forEach(folder => {
          let roomList = store.state.clientPref.find(p => p.key === folder.key);
          if (
            roomList &&
            roomList.data.children.find(
              child => child.id === room._id && child.type === 'room'
            )
          ) {
            room.folder = folder.key;
          }
        });
    });
  }

  // 新增房間資料夾
  addRoomFolderList(folderName) {
    let folderList = store.state.clientPref.find(p => p.key === 'folders-meta');
    let key = `folder_${Date.now()}`;
    folderList.data.push({
      key: key,
      name: folderName
    });
    store.state.api.me.updatePref('folders-meta', folderList.data);

    store.state.roomFolderList.push({
      key: key,
      name: folderName,
      unread: 0,
      open: false,
      drag: false,
      dragTop: 0,
      insert: false,
      moveTo: false
    });
  }

  async editRoomFolderList(folderKey, folderName) {
    let folderList = store.state.clientPref.find(p => p.key === 'folders-meta');
    let meta = folderList.data.find(p => p.key === folderKey);
    if (meta) {
      meta.name = folderName;
      await store.state.api.me.updatePref('folders-meta', folderList.data);

      let folder = store.state.roomFolderList.find(p => p.key === folderKey);
      if (folder) {
        folder.name = folderName;
      }
    }
  }

  deleteRoomFolderList(folderKey) {
    // 把房間的 folder 設為 null
    let roomList = store.state.clientPref.find(p => p.key === folderKey);
    if (roomList) {
      roomList.data.children.forEach(c => {
        if (c.type === 'room') {
          let room = store.state.roomList.find(r => r._id === c.id);
          if (room) {
            room.folder = null;
          }
        }
      });
    }

    // 刪除 pref
    let prefIdx = store.state.clientPref.findIndex(p => p.key === folderKey);
    if (~prefIdx) {
      store.state.clientPref.splice(prefIdx, 1);
    }
    store.state.api.me.deletePref(folderKey);

    // 刪除 folderList 中的 folder
    let folderListIdx = store.state.roomFolderList.findIndex(
      f => f.key === folderKey
    );
    if (~folderListIdx) {
      store.state.roomFolderList.splice(folderListIdx, 1);
    }

    // 從 folders-meta 移除房間
    let folderList = store.state.clientPref.find(p => p.key === 'folders-meta');
    let idx = folderList.data.findIndex(folder => folder.key === folderKey);
    if (~idx) {
      folderList.data.splice(idx, 1);
      store.state.api.me.updatePref('folders-meta', folderList.data);
    }
  }

  setRoomFolder(room, folderKey) {
    if (!folderKey) {
      folderKey = null;
    }

    if (room.folder) {
      // 從舊資料夾移除
      let oldFolder = store.state.clientPref.find(p => p.key === room.folder);
      if (oldFolder) {
        let idx = oldFolder.data.children.findIndex(
          r => r.type === 'room' && r.id === room._id
        );
        if (~idx) {
          oldFolder.data.children.splice(idx, 1);
          store.state.api.me.updatePref(room.folder, oldFolder.data);
        }
      }
    }

    room.folder = folderKey;

    if (room.folder) {
      // 加入新資料夾
      let newFolder = store.state.clientPref.find(p => p.key === room.folder);
      if (!newFolder) {
        newFolder = {
          key: room.folder,
          data: {
            children: []
          }
        };
        store.state.clientPref.push(newFolder);
      }
      newFolder.data.children.push({
        id: room._id,
        type: 'room'
      });
      store.state.api.me.updatePref(room.folder, newFolder.data);
    }
  }

  updateFoldersMeta(roomFolderList) {
    store.state.roomFolderList = roomFolderList;
    let oldMeta = store.state.clientPref.find(p => p.key === 'folders-meta')
      .data;
    let meta = [];
    store.state.roomFolderList.forEach(f => {
      let find = oldMeta.find(m => m.key === f.key);
      if (find) {
        meta.push(find);
      }
    });
    store.state.api.me.updatePref('folders-meta', meta);
  }

  setRoomTags(room, tags) {
    if (!tags) {
      tags = [];
    }
    room.pref.tags = tags;
    store.dispatch('updateRoomPref', {
      roomId: room._id,
      pref: {
        tags: tags
      }
    });
  }
}

export default Room;



