// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill';
import 'es6-promise/auto';
import inobounce from 'inobounce';
import Vue from 'vue';
import * as VueGoogleMaps from 'vue2-google-maps';
import firebase from '@firebase/app';
import '@firebase/messaging';

import {
  IMKitApi,
  IMKitSocket
} from 'imkit-js-api-v3/lib/imkit-js-api-v3.web.js';

import App from './App.vue';

import router from './router';
import store from './store';
import i18n from './i18n';
import filters from './filters';

import Icon from '@/components/Components/Icon/index.vue';

import { library } from '@fortawesome/fontawesome-svg-core';
import {
  faDownload,
  faEye,
  faReply,
  faEllipsisH,
  faCaretDown,
  faCaretRight
} from '@fortawesome/free-solid-svg-icons';
import {
  faFilePdf,
  faFileVideo,
  faFileImage,
  faFileAudio,
  faFileAlt,
  faCopy,
  faPaperPlane
} from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
library.add(faFilePdf);
library.add(faFileVideo);
library.add(faFileImage);
library.add(faFileAudio);
library.add(faFileAlt);
library.add(faEye);
library.add(faDownload);
library.add(faPaperPlane);
library.add(faCaretDown);
library.add(faCaretRight);
library.add(faEllipsisH);
library.add(faReply);
library.add(faCopy);
Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false;

Vue.filter('roomDisplayName', filters.roomDisplayName);
Vue.filter('roomDisplayDescription', filters.roomDisplayDescription);
Vue.filter('roomDisplayCover', filters.roomDisplayCover);

Vue.filter('messageFormat', filters.messageFormat);
Vue.filter('timeDiff', filters.timeDiff);
Vue.filter('dateFormat', filters.dateFormat);

Vue.component('Icon', Icon);

class ImkitJsSDK {
  constructor() {
    this.initEntry = false;

    // 清除 sessionStorage
    sessionStorage.clear();

    // 去他媽的 IOS
    // 處理 IOS safari 會強制忽略 user-scalable=0 的問題
    document.addEventListener(
      'touchstart',
      event => {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      },
      { passive: false }
    );

    let lastTouchEnd = 0;
    document.addEventListener(
      'touchend',
      event => {
        const now = new Date().getTime();
        if (now - lastTouchEnd <= 500) {
          event.preventDefault();
        }
        lastTouchEnd = now;
      },
      false
    );
  }
  async init(config) {
    if (this.initEntry) {
      return;
    }
    // 禁止彈性滾動
    let u = navigator.userAgent;
    let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

    if (isIOS) {
      inobounce.enable();
    }

    this.iePolyfill();

    this.initPageAncestor();
    this.setConfig(config);
    this.setApi();
    await this.sign();
    this.setSocket();

    // 擴充 Vue 功能
    this.addExtend();
    const vm = new Vue({
      el: '#IMKitApp',
      router,
      store,
      i18n,
      components: { App },
      template: '<App/>'
    });
    store.commit('setVue', vm);

    this.initEntry = true;
    return vm;
  }
  initPageAncestor() {
    let el = window;
    try {
      while (el.parent && el !== el.parent) {
        el = el.parent;
      }
      store.commit('setPageAncestor', {
        el: el,
        title: el.document.title
      });
    } catch (e) {
      el = window;
      store.commit('setPageAncestor', {
        el: el,
        title: el.document.title
      });
    }

    // store.state.pageAncestor.el.document.onfocus = () => {
    //   store.state.pageAncestor.el.document.title =
    //     store.state.pageAncestor.title;
    //   store.dispatch('updateLastRead');
    // };
    let hasFocus = true;
    setInterval(() => {
      if (store.state.pageAncestor.el.document.hasFocus()) {
        if (!hasFocus) {
          hasFocus = true;
          if (store.state.config.changeTitle) {
            store.state.pageAncestor.el.document.title =
              store.state.pageAncestor.title;
          }
          if (store.state.roomActive && store.state.roomActive.unread) {
            store.dispatch('updateLastRead');
          }
        }
      } else {
        hasFocus = false;
      }
    }, 100);
  }
  setConfig(config) {
    // 移除網址後多餘的斜線
    while (config.domain.slice(-1) === '/') {
      config.domain = config.domain.slice(0, -1);
    }
    while (config.authBase.slice(-1) === '/') {
      config.authBase = config.authBase.slice(0, -1);
    }
    while (config.urlPreviewApi.slice(-1) === '/') {
      config.urlPreviewApi = config.urlPreviewApi.slice(0, -1);
    }
    store.commit('setConfig', config);
    if (config.lang === 'auto') {
      let lang = (
        navigator.language || navigator.browserLanguage
      ).toLowerCase();
      if (!~['zh-tw', 'ja'].indexOf(lang)) {
        lang = 'en';
      }
      config.lang = lang;
    }
    store.commit('setConfigLang', config.lang);

    // 偵測是否行動裝置
    try {
      document.createEvent('TouchEvent');
      store.commit('setIsMobile', true);
    } catch (e) {
      store.commit('setIsMobile', false);
    }
  }
  setApi() {
    const api = new IMKitApi({
      domain: store.state.config.domain,
      clientKey: store.state.config.clientKey,
      token: store.state.config.token,
      privateKey: store.state.config.privateKey,
      bucketName: store.state.config.bucketName,
      authBase: store.state.config.authBase,
      urlPreviewApi: store.state.config.urlPreviewApi,
      events: store.state.config.events
    });
    store.commit('setApi', api);
  }
  async sign() {
    if (store.state.config.authClientId && store.state.api.config.authBase) {
      // 需要用 authBase API 取得 token
      let signRes = await store.state.api.auth.sign({
        id: store.state.config.authClientId
      });
      store.commit('setConfigToken', signRes.token);
    }
  }
  setSocket() {
    const socket = new IMKitSocket({
      domain: store.state.config.domain,
      token: store.state.config.token,
      privateKey: store.state.config.privateKey,
      events: store.state.config.events,
      onConnect: async () => {
        store.state.vue.$log.info('[Socket Connect]');
        store.commit('setConnect', true);
        // 取得個人資訊
        let chatInRes = await store.state.socket.chatIn();
        store.commit('setClient', chatInRes);
        let me = await store.state.api.me.getInfo();
        store.commit('setClient', Object.assign(me, chatInRes));
        // 取得個人 pref
        let pref = await store.state.api.me.getPref();
        store.commit('setClientPref', pref);
        // 取得房間列表
        await store.dispatch('getRoomList', { fromConnect: true });
        store.commit('setPageLoading', false);
        // 取得是否已開離線通知
        let enableNotification = localStorage.getItem(
          `IMKit-enableNotification-${store.state.client.client}`
        );
        store.commit('setEnableNotification', enableNotification === '1');
      },
      onDisconnect: () => {
        store.state.vue.$log.warn('[Socket Disconnect]');
        store.commit('setConnect', false);
      },
      onReceiveMessage: message => {
        store.state.vue.$log.info('[Socket ReceiveMessage]', message);
        store.dispatch('receiveMessage', message);
      },
      onReceiveRoom: room => {
        store.state.vue.$log.info('[Socket ReceiveRoom]', room);
        store.dispatch('receiveRoom', room);
      },
      onReceiveTyping: typing => {
        store.state.vue.$log.info('[Socket ReceiveTyping]', typing);
        store.dispatch('receiveTyping', typing);
      },
      onReceivelastRead: data => {
        store.state.vue.$log.info('[Socket ReceivelastRead]', data);
        store.dispatch('receivelastRead', data);
      },
      onReceiveRoomPref: data => {
        store.state.vue.$log.info('[Socket ReceiveRoomPref]', data);
        store.dispatch('receiveRoomPref', data);
      },
      onReceiveMyPrefChange: data => {
        store.dispatch('receiveMyPrefChange', data);
      },
      onReceiveMyPrefDelete: data => {
        store.dispatch('receiveMyPrefDelete', data);
      }
    });
    store.commit('setSocket', socket);
  }
  addExtend() {
    // 非 debug 模式不顯示 console.log 訊息
    Vue.prototype.$log = {
      test: (...msg) => {
        this.log('Test', 'green', msg);
      },
      error: (...msg) => {
        this.log('Error', 'red', msg);
      },
      info: (...msg) => {
        this.log('Info', 'DodgerBlue', msg);
      },
      warn: (...msg) => {
        this.log('Warn', 'orange', msg);
      }
    };
    Vue.use(VueGoogleMaps, {
      load: {
        key: store.state.config.googleApiKey,
        libraries: 'places,drawing,visualization'
      }
    });
    // i18n 擴充
    if (store.state.config.i18n) {
      for (let lang in store.state.config.i18n) {
        i18n.mergeLocaleMessage(lang, store.state.config.i18n[lang]);
      }
    }
    // 在線推播，要權限
    if ('Notification' in window) {
      Notification.requestPermission();
    }
    // 離線推播，判斷有沒有這功能，有的話註冊PWA
    if (store.state.config.FCMConfig) {
      firebase.initializeApp(store.state.config.FCMConfig);
      if ('serviceWorker' in navigator && 'PushManager' in window) {
        store.commit('setSupportNotification', true);
        Vue.prototype.$fcm = firebase.messaging();
        navigator.serviceWorker
          .register('static/sw.js')
          .then(registration => {
            Vue.prototype.$fcm.useServiceWorker(registration);

            // 點了推播，如果聊天室正開啟，跳到推播的房間
            navigator.serviceWorker.addEventListener('message', event => {
              store.dispatch('setRoomActive', event.data);
            });
          })
          .catch(error => {
            Vue.prototype.$log.error(
              '[FCM navigator.serviceWorker.register]',
              error
            );
          });
      }
    }
  }
  log(type, color, msg) {
    if (store.state.config.debug) {
      console.log(
        `%c[IMKit]%c [${type}]`,
        'background-color: Coral; color: White;',
        `color: ${color};`,
        ...msg
      );
    }
  }
  setDebug(debug) {
    store.commit('setConfigDebug', debug);
  }
  getApi() {
    return store.state.api;
  }
  iePolyfill() {
    if (
      typeof NodeList !== 'undefined' &&
      NodeList.prototype &&
      !NodeList.prototype.forEach
    ) {
      // Yes, there's really no need for `Object.defineProperty` here
      NodeList.prototype.forEach = Array.prototype.forEach;
      if (
        typeof Symbol !== 'undefined' &&
        Symbol.iterator &&
        !NodeList.prototype[Symbol.iterator]
      ) {
        Object.defineProperty(NodeList.prototype, Symbol.iterator, {
          value: Array.prototype[Symbol.itereator],
          writable: true,
          configurable: true
        });
      }
    }
  }
}

const imkitJsSDK = new ImkitJsSDK();
const {
  init,
  initPageAncestor,
  setConfig,
  setApi,
  sign,
  setSocket,
  addExtend,
  log,
  setDebug,
  getApi,
  iePolyfill
} = imkitJsSDK;

const IMKitUI = {
  init,
  initPageAncestor,
  setConfig,
  setApi,
  sign,
  setSocket,
  addExtend,
  log,
  setDebug,
  getApi,
  iePolyfill
};

window.ImkitJsSDK = IMKitUI;
export {
  init,
  initPageAncestor,
  setConfig,
  setApi,
  sign,
  setSocket,
  addExtend,
  log,
  setDebug,
  getApi,
  iePolyfill
};



