import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'AddFolder',
  props: ['editFolder'],
  data() {
    return {
      name: ''
    };
  },
  components: {
    Modal
  },
  mounted() {
    if (this.editFolder) {
      this.name = this.editFolder.name;
    }
  },
  methods: {
    create() {
      this.addRoomFolderList(this.name);
      this.cancel();
    },
    save() {
      this.editRoomFolderList({
        folderKey: this.editFolder.key,
        folderName: this.name
      });
      this.cancel();
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['addRoomFolderList', 'editRoomFolderList'])
  }
};



