import FileSaver from 'file-saver';
import localForage from 'localforage';
import lib from '@/lib';

export default {
  name: 'FileLoader',
  props: [
    'isHideInfo',
    'type',
    'url',
    'originalUrl',
    'width',
    'height',
    'extra',
    'message',
    'isThumbnail',
    'isVideoThumbnail'
  ],
  data() {
    return {
      isShowPreview: false,
      signedUrl: '',
      isComplete: true,
      isError: false,
      blob: null,
      blobDownload: null,
      objectURL: '',
      progress: 0
    };
  },
  mounted() {
    if (this.type !== 'file') {
      this.getObjectURL(this.url, false);
    } else if (
      ~['pdf', 'doc', 'docx', 'ppt', 'pptx', 'xls', 'xlsx'].indexOf(
        this.parseExtra.fileExtension
      )
    ) {
      this.preview();
    }
  },
  components: {},
  computed: {
    parseExtra() {
      return this.extra ? JSON.parse(this.extra) || {} : {};
    },
    icon() {
      if (this.isVideoThumbnail || this.type === 'video') {
        return 'file-video';
      } else if (this.type === 'image') {
        return 'file-image';
      } else if (this.type === 'audio') {
        return 'file-audio';
      } else if (this.parseExtra.fileExtension === 'pdf') {
        return 'file-pdf';
      }
      return 'file-alt';
    },
    fileSize() {
      if (!this.parseExtra.bytes) {
        return 0;
      }
      let bytes = this.parseExtra.bytes;
      let thresh = 1024;
      if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
      }
      let units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
      var u = -1;
      do {
        bytes /= thresh;
        ++u;
      } while (Math.abs(bytes) >= thresh && u < units.length - 1);
      return bytes.toFixed(1) + ' ' + units[u];
    },
    imageMaxWidth() {
      return this.isThumbnail
        ? this.$store.state.config.chat.thumbnailSize + 'px'
        : null;
    }
  },
  methods: {
    async getObjectURL(url, isDownload) {
      if (!url) {
        return;
      }
      this.isComplete = false;
      let filePath = url.split('/');
      let fileId = filePath.pop();
      let bucketName = filePath.pop();

      let data = await localForage.getItem(`IMKit-${fileId}`);
      if (data) {
        this.setObjectURL(data, isDownload);
      } else {
        try {
          let headers = {};
          this.$store.state.config.downloadFileHeaders.forEach(item => {
            headers[item.name] =
              item.type === 'variable'
                ? this.$store.state.config[item.value]
                : item.value;
          });

          let res = await this.$store.state.api.file.download(
            bucketName,
            fileId,
            headers,
            e => {
              if (!e.total) {
                this.isError = true;
              }
              this.progress = Math.ceil((e.loaded * 100) / e.total);
            }
          );
          let blob = new Blob([res.data], {
            type: res.headers['content-type']
          });
          let fr = new FileReader();
          fr.onload = () => {
            let b64 = fr.result;
            localForage.setItem(`IMKit-${fileId}`, b64);
            this.setObjectURL(b64, isDownload);
          };
          fr.readAsDataURL(blob);
        } catch (e) {
          this.isError = true;
        }
      }
    },
    setObjectURL(b64, isDownload) {
      if (isDownload) {
        this.blobDownload = lib.upload.b64ToBlob(b64);
        this.isComplete = true;
      } else {
        this.blob = lib.upload.b64ToBlob(b64);
        this.objectURL = URL.createObjectURL(this.blob);
        this.isComplete = true;
      }
    },
    async download() {
      if (!this.blobDownload) {
        let url = this.url;
        if (this.type === 'image' || this.type === 'video') {
          url = this.originalUrl;
        }
        await this.getObjectURL(url, true);
      }
      let filename = `${this.message || ''}${
        this.parseExtra.fileExtension ? '.' + this.parseExtra.fileExtension : ''
      }`;
      setTimeout(() => {
        FileSaver.saveAs(this.blobDownload, filename);
      }, 1000);
    },
    async preview() {
      if (!this.signedUrl) {
        let filePath = this.url.split('/');
        let fileId = filePath.pop();
        let bucketName = filePath.pop();
        let headers = {};
        this.$store.state.config.downloadFileHeaders.forEach(item => {
          headers[item.name] =
            item.type === 'variable'
              ? this.$store.state.config[item.value]
              : item.value;
        });
        let res = await this.$store.state.api.file.getFileSignedUrl(
          bucketName,
          fileId,
          headers
        );
        this.signedUrl = `https://docs.google.com/viewer?url=${encodeURIComponent(
          res
        )}`;
      }
    }
  }
};



