import Emoji from '@/components/RoomChat/RoomChatInput/Actions/Emoji/index.vue';
import Sticker from '@/components/RoomChat/RoomChatInput/Actions/Sticker/index.vue';
import FileUpload from '@/components/RoomChat/RoomChatInput/Actions/FileUpload/index.vue';
import Location from '@/components/RoomChat/RoomChatInput/Actions/Location/index.vue';
import Recorder from '@/components/RoomChat/RoomChatInput/Actions/Recorder/index.vue';
import PaymentRequest from '@/components/RoomChat/RoomChatInput/Actions/PaymentRequest/index.vue';
import Questionare from '@/components/RoomChat/RoomChatInput/Actions/Questionare/index.vue';
import Group from '@/components/RoomChat/RoomChatInput/Actions/Group/index.vue';

export default {
  name: 'Actions',
  props: ['actions'],
  data() {
    return {};
  },
  components: {
    Emoji,
    Sticker,
    FileUpload,
    Location,
    Recorder,
    PaymentRequest,
    Questionare,
    Group
  },
  directives: {},
  computed: {},
  methods: {
    outputScrollToBottom() {
      this.$emit('outputScrollToBottom');
    },
    sendSticker(msg) {
      this.$emit('sendSticker', msg);
    },
    insertEmoji(emoji) {
      this.$emit('insertEmoji', emoji);
    },
    sendMessage(msg) {
      this.$emit('sendMessage', msg);
    },
    closeActionsGroup() {
      let group = this.$refs.group;
      if (group) {
        if (group.length) {
          group.forEach(_group => {
            _group.hideSub();
          });
        } else {
          group.hideSub();
        }
      }
      this.$emit('closeActionsGroup');
    }
  }
};



