import { mapActions, mapGetters } from 'vuex';
import Swiper from 'swiper/dist/js/swiper.js';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomChatOutputMessage from '@/components/RoomChat/RoomChatOutput/RoomChatOutputMessage/index.vue';
import RoomChatOutputSilder from '@/components/RoomChat/RoomChatOutput/RoomChatOutputSilder/index.vue';

export default {
  name: 'RoomChatOutput',
  data() {
    return {
      unsubscribe: null,
      showMessages: true,
      newMessage: null,
      slider: {
        show: false,
        default: ''
      },
      quickReply: { items: [] },
      quickReplySlider: null,
      showQuickReply: true
    };
  },
  components: {
    Scrollbar,
    RoomChatOutputMessage,
    RoomChatOutputSilder
  },
  computed: {
    messages() {
      if (this.roomChat) {
        return this.roomChat.messages;
      } else {
        return null;
      }
    },
    _quickReply() {
      return this.$store.state.roomActive &&
        this.$store.state.roomActive.lastMessage &&
        this.$store.state.roomActive.lastMessage.template
        ? this.$store.state.roomActive.lastMessage.template
        : { items: [] };
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
    ...mapGetters(['roomChat'])
  },
  watch: {
    _quickReply(val) {
      if (val.items.length) {
        this.quickReply = val;
        if (this.quickReplySlider) {
          this.$nextTick(() => {
            this.swiperInit();
            this.quickReplySlider.update();
          });
        }
      }
    }
  },
  mounted() {
    this.$nextTick(async () => {
      if (this.$refs.list) {
        this.$refs.list.scrollBottom(0);
      }
    });
    this.unsubscribe = this.$store.subscribe((mutation, state) => {
      switch (mutation.type) {
        // 切換房間：
        // 如果是手機板，delay 一下等 list 動畫跑完再顯示訊息
        case 'setRoomActive':
          if (document.body.offsetWidth <= this.$store.state.widthPhone) {
            this.showMessages = false;
            setTimeout(() => {
              this.showMessages = true;
              this.$nextTick(() => {
                if (this.$refs.list) {
                  if (
                    this.$refs.systemUnread &&
                    (this.$refs.systemUnread.message ||
                      this.$refs.systemUnread.length)
                  ) {
                    let systemUnread =
                      this.$refs.systemUnread[0] || this.$refs.systemUnread;
                    this.$refs.list.scrollElement(systemUnread.$el);
                    return;
                  }
                  this.$refs.list.scrollBottom(0);
                }
              });
            }, 500);
          }
          break;
        // 重新載入所有聊天：
        // 卷軸滾到最下面
        case 'setRoomChatListItem':
          this.$nextTick(() => {
            if (this.$refs.list) {
              if (
                this.$refs.systemUnread &&
                (this.$refs.systemUnread.message ||
                  this.$refs.systemUnread.length)
              ) {
                let systemUnread =
                  this.$refs.systemUnread[0] || this.$refs.systemUnread;
                this.$refs.list.scrollElement(systemUnread.$el);
                return;
              }
              this.$refs.list.scrollBottom(0);
            }
          });
          break;
        // 往上載入更多
        // 先記錄 scrollBottom，載入完移回來
        case 'prependRoomChatListItemMessages':
          if (this.$refs.list) {
            let prependMessagesScrollBottom = this.$refs.list.scrollBottom();
            this.$nextTick(() => {
              this.$refs.list.scrollBottom(prependMessagesScrollBottom);
            });
          }
          break;
        case 'appendRoomChatListItemMessage':
          if (this.$refs.list) {
            let appendMessageScrollBottom = this.$refs.list.scrollBottom();
            this.$nextTick(async () => {
              if (appendMessageScrollBottom < 10) {
                this.$refs.list.scrollBottom(0);
                // await lib.message.textToUrlType(mutation.payload);
                this.$refs.list.scrollBottom(0);
              } else {
                // lib.message.textToUrlType(mutation.payload);
                // 如果送訊息的不是自己，提示有新訊息
                if (
                  mutation.payload.sender &&
                  mutation.payload.sender.id !== this.$store.state.client.client
                ) {
                  this.newMessage = mutation.payload;
                }
              }
            });
          }
          break;
        case 'checkScrollToBottom':
          if (this.$refs.list) {
            let appendMessageScrollBottom = this.$refs.list.scrollBottom();
            this.$nextTick(async () => {
              if (appendMessageScrollBottom < 10) {
                this.$refs.list.scrollBottom(0);
              }
            });
          }
          break;
      }
    });
    this.$nextTick(() => {
      this.swiperInit();
    });
  },
  beforeDestroy() {
    this.unsubscribe();
  },
  methods: {
    resize() {},
    swiperInit() {
      if (!this.$refs.sliderItem) {
        setTimeout(() => {
          this.swiperInit();
        }, 100);
        return;
      }

      this.quickReplySlider = new Swiper(this.$refs.sliderItem, {
        spaceBetween: 10,
        slidesPerView: 'auto',
        touchRatio: 0.2,
        freeMode: true,
        mousewheel: true,
        centerInsufficientSlides: true
      });
    },
    onQuickReplyClick(item) {
      if (item.action.type === 'message') {
        this.sendMessage({
          messageType: 'text',
          message: item.action.text
        });
      }
    },
    onListScroll() {
      // 往上滑到頂
      if (
        this.$refs.list.hasScroll &&
        this.$refs.list.scrollTop() < 50 &&
        this.$refs.list.scrollTop() > 0 && // 避免一載入就觸發
        !this.roomChat.hasBegin &&
        !this.roomChat.isLoadingBefore
      ) {
        this.getRoomMessagesBefore();
      }

      // 往下滑到底
      if (this.$refs.list.scrollBottom() < 10) {
        this.showQuickReply = true;
        this.newMessage = null;
      } else {
        this.showQuickReply = false;
      }
    },
    scrollToBottom() {
      this.$refs.list.scrollBottom(0);
    },
    showSilder(message) {
      this.slider.default = message.id;
      this.slider.show = true;
    },
    closeSilder() {
      this.slider.show = false;
    },
    setReply(message) {
      this.$emit('setReply', message);
    },
    gotoMessage(messageId) {
      let messageElement = document.getElementById(`message-${messageId}`);
      let contentBox = document.querySelector(
        `#message-${messageId} .contentBox`
      );
      if (messageElement) {
        this.$refs.list.scrollElement(messageElement);
        if (contentBox.classList) {
          contentBox.classList.add('shake');
          setTimeout(() => {
            contentBox.classList.remove('shake');
          }, 2000);
        }
      }
    },
    ...mapActions(['getRoomMessagesBefore', 'sendMessage'])
  }
};



