import ClickOutside from 'vue-click-outside';
import Actions from '@/components/RoomChat/RoomChatInput/Actions/index.vue';

export default {
  name: 'Group',
  props: ['action'],
  data() {
    return {
      isShowSub: true,
      alignX: '',
      boxWidth: 0
    };
  },
  components: {
    Actions
  },
  directives: {
    ClickOutside
  },
  computed: {},
  mounted() {
    this.isShowSub = false;
  },
  methods: {
    showSub() {
      if (!this.isShowSub) {
        let btn = this.$refs.btn;
        while (btn.length) {
          btn = btn[0];
        }
        let btnWidth = btn.offsetWidth;
        let pos = this.getPosition(btn);
        let roomChatWidth =
          document.querySelector('.roomChat').offsetWidth - 25;
        pos.x += 10;
        if (pos.x > roomChatWidth / 2) {
          this.boxWidth = (Math.floor(pos.x / btnWidth) + 1) * btnWidth + 2;
          this.alignX = 'right';
        } else {
          this.boxWidth =
            Math.floor((roomChatWidth - pos.x) / btnWidth) * btnWidth + 2;
          this.alignX = 'left';
        }
      }
      this.isShowSub = !this.isShowSub;
    },
    hideSub() {
      this.isShowSub = false;
    },
    getPosition(element) {
      let x = 0;
      let y = 0;
      while (!element.classList.contains('roomChat')) {
        x += element.offsetLeft - element.scrollLeft + element.clientLeft;
        y += element.offsetTop - element.scrollTop + element.clientTop;
        element = element.offsetParent;
      }

      return { x: x, y: y };
    }
  }
};



