import lib from '@/lib';

export default {
  name: 'MdTxt',
  props: ['template', 'parentLayout', 'direction'],
  data() {
    return {};
  },
  components: {},
  computed: {
    pStyle() {
      let style = {};
      if (this.template.maxLines) {
        style['max-height'] = `${this.template.maxLines * 1.4}em`;
      }
      if (this.template.color) {
        style['color'] = this.template.color;
      }
      return style;
    },
    divClass() {
      let cls = [];

      // size
      if (!this.template.size) {
        this.template.size = 'md';
      }
      cls.push(`Ex${lib.flexMessage.renderSizeStr(this.template.size)}`);

      // align
      let align = this.template.align
        ? this.template.align.toLowerCase()
        : 'start';
      if (align === 'center') {
        cls.push('ExAlgC');
      } else if (align === 'end') {
        cls.push('ExAlgE');
      }

      // gravity
      let gravity = this.template.gravity
        ? this.template.gravity.toLowerCase()
        : 'top';
      if (gravity === 'bottom') {
        cls.push('grvB');
      } else if (gravity === 'center') {
        cls.push('grvC');
      }

      // flex
      if ('flex' in this.template) {
        cls.push(`fl${this.template.flex}`);
      }

      // wrap
      if (this.template.wrap) {
        cls.push('ExWrap');
      }

      // weight
      if (this.template.weight) {
        cls.push('ExWB');
      }

      // margin
      cls.push(
        lib.flexMessage.getClassMargin(
          this.template.margin,
          this.parentLayoutm,
          this.direction
        )
      );

      // action
      if (this.template.action) {
        cls.push('hasAction');
      }

      return cls;
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {},
    doAction(action) {
      this.$emit('doAction', action);
    }
  }
};



