import { mapActions } from 'vuex';
export default {
  name: 'FileUploader',
  props: [
    'id',
    'cancelSource',
    'type',
    'url',
    'width',
    'height',
    'progress',
    'extra',
    'message',
    'isThumbnail'
  ],
  data() {
    return {};
  },
  mounted() {},
  components: {},
  computed: {
    parseExtra() {
      return JSON.parse(this.extra);
    },
    icon() {
      if (this.isVideoThumbnail || this.type === 'video') {
        return 'file-video';
      } else if (this.type === 'image') {
        return 'file-image';
      } else if (this.type === 'audio') {
        return 'file-audio';
      } else if (this.parseExtra.fileExtension === 'pdf') {
        return 'file-pdf';
      }
      return 'file-alt';
    },
    fileSize() {
      if (!this.parseExtra.bytes) {
        return 0;
      }
      let bytes = this.parseExtra.bytes;
      let thresh = 1024;
      if (Math.abs(bytes) < thresh) {
        return bytes + ' B';
      }
      let units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
      var u = -1;
      do {
        bytes /= thresh;
        ++u;
      } while (Math.abs(bytes) >= thresh && u < units.length - 1);
      return bytes.toFixed(1) + ' ' + units[u];
    },
    imageMaxWidth() {
      return this.isThumbnail
        ? this.$store.state.config.chat.thumbnailSize + 'px'
        : null;
    }
  },
  methods: {
    cancel() {
      this.uploadFileCancel({
        id: this.id,
        cancelSource: this.cancelSource
      });
    },
    ...mapActions(['uploadFileCancel'])
  }
};



