import { mapActions } from 'vuex';
import axios from 'axios';
import RoomChatInput from '@/components/RoomChat/RoomChatInput/index.vue';
import RoomChatOutput from '@/components/RoomChat/RoomChatOutput/index.vue';
import RoomChatSearchResult from '@/components/RoomChat/RoomChatSearchResult/index.vue';
import RoomChatForwardRooms from '@/components/RoomChat/RoomChatForwardRooms/index.vue';

export default {
  name: 'RoomChat',
  data() {
    return {
      searchText: '',
      searchTextTimer: null,
      searchCancelSource: null,
      drag: false
    };
  },
  components: {
    RoomChatInput,
    RoomChatOutput,
    RoomChatSearchResult,
    RoomChatForwardRooms
  },
  computed: {
    searchData() {
      return this.$store.state.roomChatSearch;
    }
  },
  methods: {
    resize() {
      this.$log.test('[RoomChat resize]');
    },
    setReply(message) {
      this.$refs.roomChatInput.setReply(message);
    },
    back() {
      this.$emit('back');
    },
    toggleInfo() {
      this.$emit('toggleInfo');
    },
    dragover(e) {
      if (!this.$store.state.config.chat.dragdropUpload.enable) {
        return;
      }
      e.preventDefault();
      this.drag = true;
    },
    dragleave(e) {
      e.preventDefault();
      if (e.target.id === 'dropzone') {
        this.drag = false;
      }
    },
    dragdrop(e) {
      if (!this.$store.state.config.chat.dragdropUpload.enable) {
        return;
      }
      e.preventDefault();
      this.drag = false;
      if (e.target.id !== 'dropzone') {
        e.dataTransfer.effectAllowed = 'none';
        e.dataTransfer.dropEffect = 'none';
      } else {
        this.uploadFiles({
          event: e,
          extra: this.$store.state.config.chat.dragdropUpload.extra
        });
      }
    },
    clearForward() {
      this.$store.state.forward.messages = [];
      this.$store.state.forward.rooms = [];
      this.$store.state.forward.selectMessages = false;
      this.$store.state.forward.selectRooms = false;
      this.$store.state.forward.sending = false;
    },
    setForwardRooms() {
      this.$store.state.forward.rooms = [];
      this.$store.state.forward.selectRooms = true;
    },
    sendForward() {},
    outputScrollToBottom() {
      this.$refs.roomChatOutput.scrollToBottom();
    },
    async search() {
      if (this.searchCancelSource) {
        this.searchCancelSource.cancel('Search canceled by the user.');
        this.searchCancelSource = null;
      }
      if (this.searchText === '') {
        return;
      }
      this.searchCancelSource = axios.CancelToken.source();
      this.setRoomChatSearch({
        result: [],
        indexOfResult: 0,
        messagesCancelSource: this.searchCancelSource,
        messages: [],
        showMessages: true,
        hasBefore: false,
        hasAfter: false,
        loadBefore: false,
        loadAfter: false
      });
      let resp = await this.roomListSearch({
        type: ['messages'],
        keyword: this.searchText,
        room: this.$store.state.roomActive._id,
        cancelToken: this.searchCancelSource.token
      });
      this.searchCancelSource = null;
      let messages = resp.messages.find(
        m => m.room._id === this.$store.state.roomActive._id
      );

      this.setRoomChatSearch({
        showInput: false,
        keyword: this.searchText,
        result: messages ? messages.messages : [],
        indexOfResult: messages ? 0 : -1,
        messagesCancelSource: null
      });
      if (messages) {
        this.searchMessages();
      }
    },
    async onSearchBarNext() {
      if (this.searchData.indexOfResult >= this.searchData.result.length - 1) {
        return;
      }
      this.setRoomChatSearch({
        indexOfResult: this.searchData.indexOfResult + 1
      });
      this.searchMessages();
    },
    async onSearchBarPrev() {
      if (this.searchData.indexOfResult <= 0) {
        return;
      }
      this.setRoomChatSearch({
        indexOfResult: this.searchData.indexOfResult - 1
      });
      this.searchMessages();
    },
    async searchMessages() {
      let messagesCancelSource = axios.CancelToken.source();
      this.setRoomChatSearch({
        messagesCancelSource: messagesCancelSource,
        hasBefore: false,
        hasAfter: false,
        loadBefore: false,
        loadAfter: false
      });
      let resp = await this.roomChatSearch({
        roomId: this.$store.state.roomActive._id,
        messageId: this.searchData.result[this.searchData.indexOfResult],
        cancelToken: messagesCancelSource.token
      });
      this.setRoomChatSearch({
        messagesCancelSource: null,
        messages: resp.data,
        hasBefore: true,
        hasAfter: true
      });
      this.$nextTick(() => {
        this.$store.commit('gotoMessage');
      });
    },
    ...mapActions([
      'uploadFiles',
      'hideRoomChatSearch',
      'setRoomChatSearch',
      'roomChatSearch',
      'roomListSearch'
    ])
  }
};



