import deepExtend from 'deep-extend';
import lib from '@/lib';

export default {
  // vue
  setVue(state, vue) {
    state.vue = vue;
  },
  // config
  setConfig(state, config) {
    deepExtend(state.config, config);
  },
  // config.debug
  setConfigDebug(state, debug) {
    state.config.debug = debug;
  },
  // config.token
  // 同時更新 api 及 socket 的 token
  setConfigToken(state, token) {
    state.config.token = token;
    if (state.api && state.api.config) {
      state.api.config.token = token;
    }
    if (state.socket && state.socket.config) {
      state.socket.config.token = token;
    }
  },
  // config.lang
  // 同時切換 i18n 語系
  setConfigLang(state, lang) {
    state.config.lang = lang;
    if (state.i18n && state.i18n.locale) {
      state.i18n.locale = lang;
    }
  },
  // pageAncestor
  setPageAncestor(state, pageAncestor) {
    state.pageAncestor = pageAncestor;
  },
  // pageLoading
  setPageLoading(state, pageLoading) {
    state.pageLoading = pageLoading;
  },
  // api
  setApi(state, api) {
    state.api = api;
  },
  // socket
  setSocket(state, socket) {
    state.socket = socket;
  },
  // connect
  setConnect(state, connect) {
    state.connect = connect;
  },
  // i18n
  setI18n(state, i18n) {
    state.i18n = i18n;
  },
  // alert
  setAlert(state, alert) {
    state.alert = alert;
  },
  // confirm
  setConfirm(state, confirm) {
    state.confirm = confirm;
  },
  // client
  setClient(state, client) {
    state.client = client;
    if (state.api && state.api.config) {
      state.api.config.clientId = client.client;
    }
    if (state.socket && state.socket.config) {
      state.socket.config.clientId = client.client;
    }
  },
  // clientPref
  setClientPref(state, pref) {
    if (!pref.find(p => p.key === 'folders-meta')) {
      pref.push({
        key: 'folders-meta',
        data: []
      });
    }
    state.clientPref = pref;

    let folderList = state.clientPref.find(p => p.key === 'folders-meta');
    state.roomFolderList = folderList.data.map(folder => {
      return {
        key: folder.key,
        name: folder.name,
        unread: 0,
        open: false,
        drag: false,
        dragTop: 0,
        insert: false,
        moveTo: false
      };
    });
  },
  setSupportNotification(state, supportNotification) {
    state.supportNotification = supportNotification;
  },
  setEnableNotification(state, enableNotification) {
    state.enableNotification = enableNotification;
  },
  // roomList
  setRoomList(state, roomList) {
    roomList.forEach(room => {
      lib.room.fullKeys(room);
    });
    state.roomList = roomList;
  },
  pushRoomList(state, room) {
    lib.room.fullKeys(room);
    state.roomList.push(room);
  },
  unshiftRoomList(state, room) {
    lib.room.fullKeys(room);
    state.roomList.unshift(room);
  },
  concatRoomList(state, roomList) {
    roomList.forEach(room => {
      lib.room.fullKeys(room);
    });
    state.roomList = state.roomList.concat(roomList);
  },
  // roomListLoader
  setRoomListLoader(state, roomListLoader) {
    state.roomListLoader = roomListLoader;
  },
  // roomActive
  setRoomActive(state, roomActive) {
    if (roomActive.id !== state.config.defaultRoom) {
      // 自訂事件
      let onSetRoomActive = state.config.events.onSetRoomActive;
      if (onSetRoomActive) {
        onSetRoomActive(roomActive);
      }
      // 是否關閉預設事件
      if (!state.config.events.onSetRoomActiveDefault) {
        return;
      }
    }

    state.roomActive = roomActive;
  },
  // roomChatList
  setRoomChatList(state, roomChatList) {
    state.roomChatList = roomChatList;
  },
  // roomChatList[n]
  setRoomChatListItem(state, roomChat) {
    let find = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === roomChat.roomId;
    });
    lib.message.messagesTimespan(roomChat.messages);
    lib.message.messagesGroup(roomChat.messages);
    if (find) {
      Object.assign(find, roomChat);
    } else {
      state.roomChatList.push(roomChat);
    }
  },
  // roomChatListItem[n][attributes]
  setRoomChatListItemAttribute(state, roomChat) {
    let find = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === roomChat.roomId;
    });
    if (find) {
      Object.assign(find, roomChat);
    }
  },
  // roomChatList[n] 讀取更多
  prependRoomChatListItemMessages(state, roomChat) {
    let find = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === roomChat.roomId;
    });

    if (find && roomChat.messages) {
      find.messages = roomChat.messages.concat(find.messages);
      lib.message.messagesTimespan(find.messages);
      lib.message.messagesGroup(find.messages);
    }
  },
  // roomChatList[n] 收到單一訊息，或自己送訊息出去(sending)
  appendRoomChatListItemMessage(state, message) {
    let find = state.roomChatList.find(_roomChat => {
      return _roomChat.roomId === message.room;
    });

    if (find) {
      find.messages.push(message);
      lib.message.messagesTimespan(find.messages);
      lib.message.messagesGroup(find.messages);
    }
  },
  checkScrollToBottom() {},
  gotoMessage() {},
  // isMobile
  setIsMobile(state, data) {
    state.isMobile = data;
  }
};



