import { mapActions } from 'vuex';
export default {
  name: 'FileUpload',
  props: ['action'],
  data() {
    return {};
  },
  components: {},
  directives: {},
  computed: {},
  methods: {
    selectFiles(event) {
      this.$emit('outputScrollToBottom');
      this.uploadFiles({
        event: event,
        extra: this.action.extra
      });
      this.$emit('closeActionsGroup');
    },
    ...mapActions(['uploadFiles'])
  }
};



