import { mapActions } from 'vuex';
import Swiper from 'swiper/dist/js/swiper.js';
export default {
  name: 'TemplateCarousel',
  props: ['template'],
  data() {
    return {};
  },
  components: {},
  computed: {
    columns() {
      return this.template.columns || [this.template];
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let titleMaxH = this.$refs.title.reduce((prev, item) => {
        return prev > item.offsetHeight ? prev : item.offsetHeight;
      }, 0);
      this.$refs.title.forEach(item => {
        item.style.height = `${titleMaxH}px`;
      });
      let textMaxH = this.$refs.text.reduce((prev, item) => {
        return prev > item.offsetHeight ? prev : item.offsetHeight;
      }, 0);
      this.$refs.text.forEach(item => {
        item.style.height = `${textMaxH}px`;
      });

      this.swiperMain = new Swiper(this.$refs.swiper, {
        freeMode: true,
        slidesPerView: 'auto',
        spaceBetween: 10
      });
    },
    sendActionMessage(action) {
      let msg = {
        messageType: 'text',
        message: action.text || action.displayText || action.label
      };
      this.sendMessage(msg);
      this.$nextTick(() => {
        this.$emit('scrollToBottom');
      });
    },
    ...mapActions(['sendMessage'])
  }
};



